@import 'vars';
.paragraph-header-home{
  background:url('../img/fons.jpg') no-repeat center center;
  background-size:cover;
  padding-bottom:clamp(20px, 3vw, 50px);
  margin-top:-80px;
  padding-top:150px;
  text-align: center;
  *{
    color:#fff;
  }
  .pre-title{
    font-weight:600;
    font-size:68px;
    text-transform: uppercase;
    padding-bottom:20px;
    font-size:61px;
    span{
      display:block;
      font-weight:300;
      font-size:41px;
    }
  }
  h1{
    font-size: 17px;
    font-weight: 400;
    line-height:1.3em;
    padding-bottom:30px;
  }

  .video{
    padding:20px 0;
    position:relative;
    &:after{
      content:"";
      width:100px;
      height:100px;
      border-radius:50%;
      background:url('../img/play.svg') no-repeat center center $green;
      position:absolute;
      top:0;
      bottom:0;
      left:0;
      right:0;
      z-index:3;
      margin-left:auto;
      margin-right:auto;
      margin-top:auto;
      margin-bottom:auto;
    }
  }

  .btn-cta{
    text-decoration: none;
    border:1px solid $green;
    padding:15px 30px;
    font-weight: 600;
    display:inline-block;
    margin:10px 0;
    color:$green;
    font-size:18px;
  }
}

.paragraph-image-text{
  padding-top:clamp(40px, 4vw, 60px);
  padding-bottom:clamp(20px, 2vw, 30px);
  h2{
    padding-bottom:30px;
  }
  img{
    border-radius:50%;
  }
  &.item-1{
    .col-image{
    
      position:relative;
      &:after{
        background:url('../img/icon-arrow-down.svg') no-repeat bottom right;
        position:absolute;
        z-index:2;
        width:150px;
        height:200px;
        content:"";
        bottom:-20px;
        right:40px;
      }
    }
  }
  &.item-2{
    .col-image{
      position:relative;
      &:after{
        background:url('../img/icon-arrow-down-v2.svg') no-repeat bottom right;
        position:absolute;
        z-index:2;
        width:150px;
        height:200px;
        content:"";
        bottom:-50px;
        left:-20px;
      }
    }
  }
  
}

.paragraph-grafic{
  margin-top:90px;
  padding-top:clamp(20px, 4vw, 80px);
  padding-bottom:clamp(20px, 5vw, 90px);
  background-color:$grey;
  h2{
    padding:20px 0;
  }
  img{
    padding-top:clamp(20px, 4vw, 60px);
  }
}

.paragraph-actualidad-home{
  padding-top:clamp(20px, 6vw, 90px);
  padding-bottom:clamp(20px, 6vw, 100px);
  background-color:$black;
  h2{
    padding-bottom:35px;
    text-align: center;
    text-transform: uppercase;
  }
  h3{
    font-size:30px;
    padding-bottom:20px;
    padding-top:20px;
  }
  
  .content{
    font-weight: 300;
    font-size:18px;
    p{
      font-weight: 300;
      font-size:18px;
    }
  }

  .date{
    font-size:12px;
  }
  *{
    color:#fff;
  }
}

.paragraph-highlight-green{
  background-color:$green;
  padding-top:clamp(30px, 7vw, 100px);
  padding-bottom:clamp(30px, 7vw, 100px);
  *{
    color:#fff;
    text-align: center;
  }
  h2{
    text-align: center;
  }
  .content{
    padding-top:30px;
  }

}

.paragraph-partners{
  padding-top:clamp(30px, 7vw, 100px);
  padding-bottom:clamp(30px, 7vw, 100px);
  h2{
    text-align: center;
    padding-bottom:30px;
  }

  .slick-prev, .slick-next{
    background:url('../img/btn-slick.svg') no-repeat center center;
    height:70px;
    width:70px;
    border:0;
    text-indent: -3000px;
    position:absolute;
    right:-20px;
    z-index:10;
    top:0;
  }
  .slick-prev{
    right:auto;
    left:-100px;
    transform: rotate(180deg);
  }

}